import React from "react";
import { Box, Stack, Typography } from "@mui/material";
import Rumble from "../../assets/images/icons8-rumble-48.png";
import Facebook from "../../assets/images/fbicon.png";


const Footer = () => {
  return (
    <>
      <Box
        className="flex"
        sx={{
          flexDirection: "row",
          justifyContent: "space-between",
          width: "100%",
          height: "70px",
          padding: "0 2vw",
          backgroundColor: "#3c3b6e",
          position: "relative",
          bottom: "0",
          color: "#fff",
        }}
      >
        <Stack
          className="flex"
          sx={{
            flexDirection: "column",
            justifyContent: "center",
            ml: "10px",
          }}
        >
          <Typography sx={{ ml: "8px", mt: "3px" }}>Follow Us</Typography>
          <Stack className="flex" sx={{ flexDirection: "row", ml: "5px" }}>
            <a
              href="https://rumble.com/user/AZLD19Republicans"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={Rumble}
                alt="rumble icon"
                style={{ height: "40px", width: "40px" }}
              />
            </a>
            <a
              href="https://www.facebook.com/groups/1252589155351577 "
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={Facebook}
                alt="facebook icon"
                style={{ height: "40px", width: "40px" }}
              />
            </a>
          </Stack>
        </Stack>
        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: "column",
          }}
        >
          <Typography
            sx={{
              display: { lg: "block", md: "block", sm: "none", xs: "none" },
            }}
          >
            <p>All Rights Reserved </p>
          </Typography>
          <Typography
            sx={{
              display: { lg: "block", md: "block", sm: "none", xs: "none" },
            }}
          >
            <p>Cochise County Conservatives</p>
          </Typography>
        </Stack>
        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: "column",
            mr: "10px",
          }}
        >
          <Typography>
            <p>Contact Us</p>
          </Typography>
          <Typography>
            <p>cochiserepublicanevents@gmail.com</p>
          </Typography>
        </Stack>
      </Box>
    </>
  );
};

export default Footer;
