import React from "react";
import { Stack } from "@mui/material";
import Blurb from "../components/Blurb/Blurb.jsx";
import RepsCard from "../components/Cards/RepsCard.jsx";
import ResourceCard from "../components/Cards/ResourceCard.jsx";
import map1 from "../assets/images/map1.png";
import map2 from "../assets/images/map2.png";
import map3 from "../assets/images/map3.png";
import map4 from "../assets/images/map4.png";
import Liberal from "../assets/images/liberal.jpeg";
import VoterInfo from "../assets/images/voterInfo.jpg";
import VoterPortal from "../assets/images/VoterPortal.PNG";
import { useData } from "../hooks/context-hook";

const VoterResourcesContent = () => {
  const { siteData } = useData();
  const voter = siteData.pages.voter;

  return (
    <>
      {console.log("voter", voter)}
      <Blurb text={voter?.content} />
      <Stack
        className="disFlexSpace"
        sx={{
          flexDirection: {
            lg: "row",
            md: "row",
            sm: "column",
            xs: "column",
          },
          width: "100%",
          height: "auto",
        }}
      >
        <RepsCard
          bgimg={`${process.env.REACT_APP_SERVER}${voter?.first.photo}`}
          name={voter?.first.name}
          title={voter?.first.position}
          email={voter?.first.email}
        />
        <RepsCard
          bgimg={`${process.env.REACT_APP_SERVER}${voter?.second.photo}`}
          name={voter?.second.name}
          title={voter?.second.position}
          email={voter?.second.email}
        />
        <RepsCard
          bgimg={`${process.env.REACT_APP_SERVER}${voter?.third.photo}`}
          name={voter?.third.name}
          title={voter?.third.position}
          email={voter?.third.email}
        />
      </Stack>
      <Blurb text={voter?.content2} />
      <Stack
        className="flex"
        sx={{
          height: "auto",
          width: "100%",
          flexDirection: {
            lg: "row",
            md: "row",
            sm: "column",
            xs: "column",
          },
          justifyContent: {
            lg: "space-around",
            md: "space-around",
            sm: "center",
            xs: "center",
          },
          alignItems: "center",
        }}
      >
        <ResourceCard
          link={
            "https://cochise.maps.arcgis.com/apps/webappviewer/index.html?id=5831eb364a944560a46ca21ad04226da"
          }
          abgImg={map1}
          abuttontext={"Precinct Map"}
        />
        <ResourceCard
          link={
            "https://cochise.maps.arcgis.com/apps/webappviewer/index.html?id=5831eb364a944560a46ca21ad04226da"
          }
          abgImg={map2}
          abuttontext={"Supervisors"}
        />
        <ResourceCard
          link={
            "https://irc-az.maps.arcgis.com/apps/instant/lookup/index.html?appid=424810a4667049388ef6df4f0c73098b"
          }
          abgImg={map3}
          abuttontext={"Legislative"}
        />
        <ResourceCard
          link={
            "https://irc-az.maps.arcgis.com/apps/instant/lookup/index.html?appid=ffc3e1bac1ed4350923608ae8e3544c7"
          }
          abgImg={map4}
          abuttontext={"Congressional"}
        />
      </Stack>
      <Blurb text={voter?.content3} />
      <Stack
        className="flex"
        sx={{
          width: "100%",
          height: "auto",
          alignItems: "center",
          justifyContent: {
            lg: "space-around",
            md: "space-around",
            sm: "center",
            xs: "center",
          },
          flexDirection: {
            lg: "row",
            md: "row",
            sm: "column",
            xs: "column",
          },
        }}
      >
        <ResourceCard
          link={"https://my.arizona.vote/WhereToVote.aspx?s=individual"}
          abgImg={VoterPortal}
          abuttontext={"Voter Portal"}
          text={"Access to Voter Portal"}
        />
        <ResourceCard
          link={voter?.link}
          // link={"http://www.liberalorconservativequiz.org/Quiz2.htm"}
          abgImg={Liberal}
          abuttontext={"Take the Quiz"}
          text={"Liberal or Conservative?"}
        />
        <ResourceCard
          link={"https://cochise.az.gov/509/Voter-Information"}
          abgImg={VoterInfo}
          abuttontext={"Read More"}
          text={"Click below for Voter Information"}
        />
      </Stack>
    </>
  );
};

export default VoterResourcesContent;
