import { Stack, Card, Button } from "@mui/material";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import { useData } from "../../hooks/context-hook.jsx";
import React, { useEffect, useState } from "react";
import axios from "axios";
import Title from "../../components/Title/Title.jsx";
import FlagBG from "../../components/FlagBG/FlagBG.jsx";

const UpdatePCResorces = () => {
  const { siteData, updateAbout } = useData();
  const pcResources = siteData.pages.resources;
  const [update, setUpdate] = useState({});

  const handleChange = (e) => {
    setUpdate((prev) => ({
      [e.target.name]: e.target.value,
    }));
  };

  const [render, setRender] = useState(1);

  useEffect(() => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_SERVER}/api/getSite`,
    })
      .then((res) => {
        updateAbout(res.data);
      })
      .catch((err) => console.log("err", err));
  }, [render]);

  const handleSubmit = (e) => {
    e.preventDefault();

    axios({
      method: "POST",
      withCredentials: true,
      data: update,
      url: `${process.env.REACT_APP_SERVER}/admin/updates/pcresources/content/662997c1178950265fde4aa4`,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })
      .catch(() => alert("update failed"));
  };

  const [pdfUpload, setPdfUpload] = useState([]);
  const handlePdfFile = (e) => {
    console.warn("handleSelectedFiles", e.target.name);
    setPdfUpload(e.target.files);
    setTargetName2(e.target.name);
  };

  const handlePdfSubmit = () => {
    console.warn("Handle PDF Submit ", targetName, pdfUpload[0]);
    const formData = new FormData();
    formData.append("what", targetName);
    formData.append("images", pdfUpload[0]);

    axios({
      method: "POST",
      withCredentials: true,
      data: formData,
      headers: { "Content-Type": "multipart/form-data" },
      url: `${process.env.REACT_APP_SERVER}/admin/updates/pcresources/pdf/${targetName2}  `,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })
      .catch((err) => console.log("err", err));
  };

  const [targetName, setTargetName] = useState("");
  const [targetName2, setTargetName2] = useState("");

  useEffect(() => {
    console.warn("useEffect, ", targetName);
    console.log(targetName.slice(0, 3));
    if (targetName && targetName.slice(0, 3) == "pdf") {
      console.warn("included");
      handleImageSubmit(targetName);
    } else {
    }
  }, [targetName]);

  useEffect(() => {
    console.warn("useEffect pdf, ", targetName2);
    handlePdfSubmit(targetName2);
  }, [targetName2]);

  const [selectedFiles, setSelectedFiles] = useState([]);
  const handleSelectedFiles = (e) => {
    console.warn("handleSelectedFiles", e.target.name);
    setSelectedFiles(e.target.files);
    setTargetName(e.target.name);
  };

  const handleImageSubmit = () => {
    const formData = new FormData();
    formData.append("what", targetName);
    formData.append("images", selectedFiles[0]);

    axios({
      method: "POST",
      withCredentials: true,
      data: formData,
      headers: { "Content-Type": "multipart/form-data" },
      url: `${process.env.REACT_APP_SERVER}/admin/updates/pcresources/image/${targetName}  `,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })
      .catch((err) => console.log("err", err));
  };

  const [nameChange, setNameChange] = useState("");
  const handleNameChange = (e) => {
    setNameChange({ [e.target.id]: e.target.value });
  };

  const handleTitleSubmit = () => {
    let path = Object.keys(nameChange)[0];
    axios({
      method: "POST",
      withCredentials: true,
      data: nameChange,
      url: `${process.env.REACT_APP_SERVER}/admin/updates/pcresources/name/${path}  `,
    }).then((res) => {
      alert(res.data.msg);
      setRender(render + 1);
    });
  };

  return (
    <>
      <NavbarAdmin />
      {console.log("pcResources';", pcResources)}
      {console.log("setNameChange';", nameChange)}
      {console.warn("tagetName2", targetName2)}
      {console.warn("pdfUpload", pdfUpload)}
      <FlagBG>
        <Title text="Resources" />
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            marginTop: "10px",
          }}
        >
          <textarea
            onChange={(e) => handleChange(e)}
            style={{ width: "50%", textAlign: "center", padding: "10px" }}
            name="content"
            id=""
            rows="5"
            placeholder={pcResources?.content}
          ></textarea>
          <Button
            variant="contained"
            className="btn"
            sx={{ m: "1em !important", width: "15%" }}
            onClick={(e) => handleSubmit(e)}
            id="pcresources"
          >
            Update
          </Button>
        </div>
        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: { lg: "row" },
            height: "auto",
            mb: "5%",
          }}
        >
          <Card
            className="bShadow disFlexCenter"
            sx={{
              flexDirection: "column",
              padding: "1em",
              width: "250px",
              height: "350px",
              backgroundColor: "#fff",
              margin: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${pcResources?.pdf1.img})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "100%",
                height: "75%",
              }}
            ></Stack>
            <div className="loadimgdiv btn">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                id="photo1"
                name="pdf1"
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name=""
                id="pdf1"
                placeholder={siteData.pages.resources?.pdf1.name}
                onChange={(e) => handleNameChange(e)}
              />
              <button onClick={(e) => handleTitleSubmit(e)} className="btn1">
                Update Title
              </button>
              <div className="loadimgdiv btn">
                Upload PDF
                <input
                  className="imguploadinput"
                  type="file"
                  onChange={(e) => handlePdfFile(e)}
                  name="pdf1"
                  id="pdf1"
                />
              </div>
            </div>
          </Card>
          <Card
            className="bShadow disFlexCenter"
            sx={{
              flexDirection: "column",
              padding: "1em",
              width: "250px",
              height: "350px",
              backgroundColor: "#fff",
              margin: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${pcResources?.pdf2.img})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "100%",
                height: "75%",
              }}
            ></Stack>
            <div className="loadimgdiv btn">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                id="photo2"
                name="pdf2"
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name=""
                id="pdf2"
                placeholder={siteData.pages.resources?.pdf2.name}
                onChange={(e) => handleNameChange(e)}
              />
              <button onClick={(e) => handleTitleSubmit(e)} className="btn1">
                Update Title
              </button>
              <div className="loadimgdiv btn">
                Upload PDF
                <input
                  className="imguploadinput"
                  type="file"
                  onChange={(e) => handlePdfFile(e)}
                  name="pdf2"
                  id="pdf2"
                />{" "}
              </div>
            </div>
          </Card>
          <Card
            className="bShadow disFlexCenter"
            sx={{
              flexDirection: "column",
              padding: "1em",
              width: "250px",
              height: "350px",
              backgroundColor: "#fff",
              margin: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${pcResources?.pdf3.img})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "100%",
                height: "75%",
              }}
            ></Stack>
            <div className="loadimgdiv">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                id="photo3"
                name="pdf3"
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name=""
                id="pdf3"
                placeholder={siteData.pages.resources?.pdf3.name}
                onChange={(e) => handleNameChange(e)}
              />
              <button onClick={(e) => handleTitleSubmit(e)} className="btn1">
                Update Title
              </button>
              <div className="loadimgdiv btn">
                Upload PDF
                <input
                  className="imguploadinput"
                  type="file"
                  onChange={(e) => handlePdfFile(e)}
                  name="pdf3"
                  id="pdf3"
                />{" "}
              </div>
            </div>
          </Card>
          <Card
            className="bShadow disFlexCenter"
            sx={{
              flexDirection: "column",
              padding: "1em",
              width: "250px",
              height: "350px",
              backgroundColor: "#fff",
              margin: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${pcResources?.pdf4.img})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "100%",
                height: "75%",
              }}
            ></Stack>
            <div className="loadimgdiv btn">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                id="photo4"
                name="pdf4"
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name=""
                id="pdf4"
                placeholder={siteData.pages.resources?.pdf4.name}
                onChange={(e) => handleNameChange(e)}
              />

              <button onClick={(e) => handleTitleSubmit(e)} className="btn1">
                Update Title
              </button>
              <div className="loadimgdiv btn">
                Upload PDF
                <input
                  className="imguploadinput"
                  type="file"
                  onChange={(e) => handlePdfFile(e)}
                  name="pdf4"
                  id="pdf4"
                />{" "}
              </div>
            </div>
          </Card>
          <Card
            className="bShadow disFlexCenter"
            sx={{
              flexDirection: "column",
              padding: "1em",
              width: "250px",
              height: "350px",
              backgroundColor: "#fff",
              margin: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${pcResources?.pdf5.img})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "100%",
                height: "75%",
              }}
            ></Stack>
            <div className="loadimgdiv btn">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                id="photo5"
                name="pdf5"
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name=""
                id="pdf5"
                placeholder={siteData.pages.resources?.pdf5.name}
                onChange={(e) => handleNameChange(e)}
              />
              <button onClick={(e) => handleTitleSubmit(e)} className="btn1">
                Update Title
              </button>
              <div className="loadimgdiv btn">
                Upload PDF
                <input
                  className="imguploadinput"
                  type="file"
                  onChange={(e) => handlePdfFile(e)}
                  name="pdf5"
                  id="pdf5"
                />{" "}
              </div>
            </div>
          </Card>
        </Stack>
      </FlagBG>
      <Footer />
    </>
  );
};

export default UpdatePCResorces;
