import { Box } from "@mui/material";
import Blurb from "../components/Blurb/Blurb.jsx";
import { useData } from "../hooks/context-hook.jsx";
import BlogCard from "../components/Cards/BlogCard.jsx";
import { useEffect, useState } from "react";

const GetInvolved = () => {
  const { siteData, updateAbout } = useData();
  const [blogPosts, setBlogPosts] = useState([]);
  let getInvolved = siteData.pages.getInvolved;

  useEffect(() => {
    fetch(`${process.env.REACT_APP_SERVER}/api/blogPosts`)
      .then((res) => res.json())
      .then((data) => setBlogPosts(data))
      .catch((err) => console.error(err));
  }, []);

  const handleDelete = (postId) => {
    setBlogPosts((prevPosts) =>
      prevPosts.filter((post) => post._id !== postId)
    );
  };

  return (
    <>
      {console.log("getInvolved", blogPosts)}
      <Blurb text="Your dedicated hub for updates on local political developments, insights from key political figures, and discussions on important issues affecting our county and country. Whether you are a resident seeking to stay informed, a voter looking for guidance, or a supporter of our cause, our articles aim to provide you with valuable information and perspectives." />
      <Box
        className="disFlexSpace"
        sx={{
          flexDirection: {
            lg: "row",
            md: "row",
            sm: "column",
            xs: "column",
          },
          width: "100%",
          height: "auto",
          padding: { lg: "0 5%", md: "0 5%", sm: "0 5%", xs: "0 5%" },
        }}
      >
        <Box
          sx={{
            display: "grid",
            gridAutoFlow: "row",
            gridTemplateColumns: {
              lg: "repeat(4, 1fr)",
              md: "repeat(3, 1fr)",
              sm: "repeat(2, 1fr)",
              xs: "column",
            },
            gridTemplateRows: "auto",
            gap: 3,
          }}
        >
          {blogPosts.map((post) => (
            <BlogCard
              key={post._id}
              title={post.title}
              author={post.author}
              blogPosts={post.blogPosts}
              date={post.date}
              postId={post._id}
              // onDelete={handleDelete}
              imageUrl={post.imageUrl}
            />
          ))}
        </Box>
      </Box>
    </>
  );
};

export default GetInvolved;
