import { Box } from "@mui/system";
import Flag from "../../assets/images/flag.jpg";

const FlagBG = ({ children }) => {
  return (
    <>
      <Box
        sx={{
          position: "relative",
          width: "100%",
          height: "100vh",
          backgroundImage: `url(${Flag})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          overflow: "auto",
        }}
      >
        {children}
      </Box>
    </>
  );
};

export default FlagBG;
