import { Typography, Button, TextField, Box } from "@mui/material";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import Title from "../../components/Title/Title";
import FlagBG from "../../components/FlagBG/FlagBG.jsx";
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useData } from "../../hooks/context-hook.jsx";
import BlogCard from "../../components/Cards/BlogCard.jsx";

const UpdateGetInvolved = () => {
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [text, setText] = useState("");
  // const [image, setSelectedFiles] = useState([]);
  const [selectedFiles, setSelectedFiles] = useState([]);
  const { siteData } = useData();
  let getInvolved = siteData.pages.getInvolved;


  const [blogPosts, setBlogPosts] = useState([]);

  useEffect(() => {
    fetch(`${process.env.REACT_APP_SERVER}/api/blogPosts`)
      .then((res) => res.json())
      .then((data) => setBlogPosts(data))
      .catch((err) => console.error(err));
  }, []);

  const handleDelete = (postId) => {
    console.log("handle del hit")
    axios({
      method: "DELETE",
      url: `${process.env.REACT_APP_SERVER}/api/blogPosts/${postId}`,
    })
      .then(() => {
        alert("Blog post deleted successfully");
      })
      .catch((err) => console.error(err));

    // add server side delete logi



    setBlogPosts((prevPosts) =>
      prevPosts.filter((post) => post._id !== postId)
    );
  };





  const handleAddPost = () => {
    const postData = new FormData();
    postData.append("title", title);
    postData.append("author", author);
    postData.append("blogPosts", text);
    // if (image) {
    // console.warn("if image hit")
    postData.append("image", selectedFiles[0]);
    // }

    axios({
      method: "POST",
      url: `${process.env.REACT_APP_SERVER}/api/blogPosts`,
      data: postData,
      headers: { "Content-Type": "multipart/form-data" },
    })
      // axios
      //   .post(`${process.env.REACT_APP_SERVER}/api/blogPosts`, { headers: { "Content-Type": "multipart/form-data" } }, postData )
      .then((res) => {
        alert("Blog post added successfully");
        setTitle("");
        setAuthor("");
        setText("");
        setSelectedFiles(null);
      })
      .catch((err) => console.error("Error adding blog post:", err));
  };

  return (
    <>
      {console.log("blogposts", blogPosts)}
      <NavbarAdmin />
      <FlagBG>
        <Title text="Get Involved" />
        <Box
          sx={{
            display: "grid",
            gridAutoColumns: "1fr",
            gridRow: "1",
            gap: 2,
            padding: "0 5%",
          }}
        >
          <Box
            className="bShadow"
            sx={{
              gridRow: "1",
              gridColumn: "1/3",
              backgroundColor: "white",
              justifyContent: "center",
              textAlign: "center",
              padding: "1em",
            }}
          >
            <Typography variant="h3" sx={{ mb: "1rem" }}>
              Add a Blog Post
            </Typography>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <TextField
                label="Title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                sx={{
                  width: { lg: "50%", md: "50%", sm: "100%", xs: "100%" },
                  mb: "1rem",
                }}
              />
              <TextField
                label="Author"
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
                sx={{
                  width: { lg: "50%", md: "50%", sm: "100%", xs: "100%" },
                  mb: "1rem",
                }}
              />
              <TextField
                label="Content"
                value={text}
                onChange={(e) => setText(e.target.value)}
                multiline
                rows={4}
                sx={{
                  width: { lg: "50%", md: "50%", sm: "100%", xs: "100%" },
                  mb: "1rem",
                }}
              />
              <input
                type="file"
                id="image"
                accept="image/*"
                onChange={(e) => setSelectedFiles(e.target.files)}
                style={{
                  marginBottom: "1rem",
                }}
              />
              <Button
                className="btn"
                variant="contained"
                onClick={handleAddPost}
                sx={{ width: "50%" }}
              >
                Add Post
              </Button>
            </div>
          </Box>
          <Box sx={{ gridRow: "1" }}>
            {blogPosts.map((post) => (
              <BlogCard
                key={post._id}
                onDelete={() => handleDelete(post._id)}
                title={post.title}
                author={post.author}
                blogPosts={post.text}
                imageUrl={post.imageUrl}
              />
            ))}
          </Box>
        </Box>
      </FlagBG>
      <Footer />
    </>
  );
};

export default UpdateGetInvolved;
