import React from "react";
import { Stack, Card, Button, Typography } from "@mui/material";

// Resource Card with background image and link to external pages (maps,quizes, Voter info etc.)
const ResourceCard = ({
  abgImg,
  pdfbgImg,
  text,
  link,
  abuttontext,
  pdfbuttontext,
}) => {
  return (
    <>
      <Card
        className="bShadow flex"
        sx={{
          flexDirection: "column",
          width: "250px",
          height: "300px",
          backgroundColor: "#fff",
          m: { lg: "10px", md: "10px", sm: "10px", xs: "10px" },
        }}
      >
        {abgImg && (
          <Stack
            sx={{
              backgroundImage: `url(${abgImg})`,
              backgroundSize: "cover",
              backgroundPosition: "center",
              width: "90%",
              height: "65%",
              m: "5%",
            }}
          ></Stack>
        )}
        {pdfbgImg && (
          <Stack
            sx={{
              backgroundImage: `url(${pdfbgImg})`,
              backgroundSize: "cover",
              backgroundPosition: "center",
              width: "90%",
              height: "75%",
              m: "5%",
            }}
          ></Stack>
        )}

        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: "column",
          }}
        >
          <Typography
            sx={{
              textAlign: "center",
              fontSize: "15px",
              m: "5px",
              color: "#3c3b6e",
            }}
          >
            <p>{text}</p>
          </Typography>
        </Stack>
        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: "column",
          }}
        >
          {abuttontext && (
            <a
              href={link}
              target="_blank"
              style={{
                width: "100%",
                justifyContent: "center",
              }}
              className="atag flex"
            >
              <Button className="btn lgBtn">{abuttontext}</Button>
            </a>
          )}

          {pdfbuttontext && (
            <a
              href={link}
              target="_blank"
              style={{
                width: "100%",
                justifyContent: "center",
              }}
              className="atag flex"
            >
              <Button className="btn lgBtn">{pdfbuttontext}</Button>
            </a>
          )}
        </Stack>
      </Card>
    </>
  );
};

export default ResourceCard;
