import { Routes, Route } from "react-router-dom";
import ContextRoute from "./components/ContextRoute/ContextRoute";
import Layout from "./pages/Layout";
import HomePage from "./pages/HomePage";
import Events from "./pages/Events";
import Subscribe from "./pages/Subscribe";
import About from "./pages/About";
import PCResources from "./pages/PCResources";
import VoterResources from "./pages/VoterResources";
import GetInvolved from "./pages/GetInvolved";
import Contact from "./pages/Contact";
import ProtectedRoute from "./components/ContextRoute/ProtectedRoute";
import AdminAccess from "./components/Admin/AdminAccess";
import CreateUser from "./Utility/CreateUser";
import UpdateAboutContent from "./pages/Admin/UpdateAboutContent";
import UpdatePCResources from "./pages/Admin/UpdatePCResources";
import UpdateContact from "./pages/Admin/UpdateContact";
import UpdateEvents from "./pages/Admin/UpdateEvents";
import UpdateGetInvolved from "./pages/Admin/UpdateGetInvolved";
import UpdateHomePage from "./pages/Admin/UpdateHomePage";
import UpdateSubscribe from "./pages/Admin/UpdateSubscribe";
import UpdateVoterResources from "./pages/Admin/UpdateVoterResources";
import "./global.css";

const App = () => {
  return (
    <Routes>
      <Route element={<ContextRoute />}>
        {/* ROOT ROUTES (USER) */}
        <Route path="/" element={<Layout />}>
          <Route index element={<HomePage />} />
          <Route path="Events" element={<Events />} />
          <Route path="Subscribe" element={<Subscribe />} />
          <Route path="About" element={<About />} />
          <Route path="PCResources" element={<PCResources />} />
          <Route path="VoterResources" element={<VoterResources />} />
          <Route path="GetInvolved" element={<GetInvolved />} />
          <Route path="Contact" element={<Contact />} />
        </Route>
        {/* PROTECTED ROUTES (ADMIN) */}
        <Route path="/admin/" element={<ProtectedRoute />}>
          <Route index element={<AdminAccess />} />
          <Route path="createUser" element={<CreateUser />} />
          <Route path="updateAbout" element={<UpdateAboutContent />} />
          <Route path="updatePCResources" element={<UpdatePCResources />} />
          <Route path="UpdateContact" element={<UpdateContact />} />
          <Route path="UpdateEvents" element={<UpdateEvents />} />
          <Route path="UpdateGetInvolved" element={<UpdateGetInvolved />} />
          <Route path="UpdateHomePage" element={<UpdateHomePage />} />
          <Route path="UpdateSubscribe" element={<UpdateSubscribe />} />
          <Route
            path="UpdateVoterResources"
            element={<UpdateVoterResources />}
          />
        </Route>
      </Route>
    </Routes>
  );
};

export default App;
