import React, { useState, useEffect } from "react";
import axios from "axios";
import { Stack, Typography, Card } from "@mui/material";
import ResourceCard from "../../components/Cards/ResourceCard.jsx";
import map1 from "../../assets/images/map1.png";
import map2 from "../../assets/images/map2.png";
import map3 from "../../assets/images/map3.png";
import map4 from "../../assets/images/map4.png";
import Liberal from "../../assets/images/liberal.jpeg";
import VoterInfo from "../../assets/images/voterInfo.jpg";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import VoterPortal from "../../assets/images/VoterPortal.PNG";
import Title from "../../components/Title/Title.jsx";
import FlagBG from "../../components/FlagBG/FlagBG.jsx";
import { useData } from "../../hooks/context-hook.jsx";

const UpdateVoterResources = () => {
  const { siteData, updateAbout } = useData();
  const voter = siteData.pages.voter;
  const [content, setContent] = useState(
    "Our representatives, all proud residents of Cochise County, are eager to hear from you. Reach out to them and share your thoughts, concerns, and ideas on how they can best serve you."
  );
  const [render, setRender] = useState(1);

  useEffect(() => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_SERVER}/api/getSite`,
    })
      .then((res) => {
        updateAbout(res.data);
      })
      .catch((err) => console.log("err", err));
  }, [render]);

  const handleUpdateContent = (e) => {
    setContent(e.target.value);
  };

  const handleSubmitContent = (e) => {
    console.warn("contentID", e.target.id, content);
    axios({
      method: "POST",
      url: `http://localhost:5000/admin/updates/voterResources/content/`,
      data: {
        [e.target.id]: content,
      },
    })
      .then((res) => {
        alert("Content Updated");
      })
      .catch((err) => console.log(err));
  };
  const [reps, setReps] = useState({
    name: "",
    email: "",
    position: "",
    id: "",
  });

  const handleUpdateReps = (e) => {
    setReps((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
      id: e.target.id,
    }));
  };

  const handleSubmitRep = (e) => {
    axios({
      method: "POST",
      url: `http://localhost:5000/admin/updates/voterResources/rep/`,
      data: reps,
    }).then((res) => {
      alert("Content Updated");
      setRender(render + 1);
    });
  };
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [imageRoute, setImageRoute] = useState("");

  const handleSelectedFiles = (e) => {
    setImageRoute(e.target.name);
    setSelectedFiles(e.target.files);
  };

  useEffect(() => {
    handleImageSubmit(imageRoute);
  }, [selectedFiles]);

  const handleImageSubmit = (imageRoute) => {
    const formData = new FormData();
    formData.append("what", imageRoute);
    formData.append("images", selectedFiles[0]);
    axios({
      method: "POST",
      withCredentials: true,
      data: formData,
      headers: { "Content-Type": "multipart/form-data" },
      url: `${process.env.REACT_APP_SERVER}/admin/updates/voterResources/img/${imageRoute}/  `,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })
      .catch((err) => console.log("err", err));
  };

  return (
    <>
      {console.log(content)}
      <NavbarAdmin />
      <FlagBG>
        <Title text="Voter Resources" />
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <textarea
            style={{
              width: "45%",
              textAlign: "center",
              marginTop: "10px",
              padding: "10px",
            }}
            name=""
            id="content"
            rows="3"
            onChange={(e) => handleUpdateContent(e)}
            placeholder="Our representatives, all proud residents of Cochise County, are eager to hear from you. 
            Reach out to them and share your thoughts, concerns, and ideas on how they can best serve you."
          ></textarea>
          <button
            id="content"
            onClick={(e) => handleSubmitContent(e)}
            style={{
              width: "200px",
              padding: "5px",
              margin: "5px",
              fontSize: "large",
            }}
            className="btn"
          >
            Update
          </button>
        </div>
        <Stack
          className="disFlexSpace"
          sx={{
            flexDirection: {
              lg: "row",
              md: "row",
              sm: "column",
              xs: "column",
            },
            width: "100%",
            height: "auto",
          }}
        >
          <Card
            className="bShadow flex"
            sx={{
              flexDirection: "column",
              alignItems: "center",
              width: "250px",
              height: "400px",
              m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${voter?.first.photo})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "90%",
                height: "75%",
                mt: "10px",
              }}
            ></Stack>
            <div className="loadimgdiv">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                name="first"
                id=""
                onChange={(e) => handleSelectedFiles(e)}
              />
            </div>
            <Typography
              sx={{
                mt: "8px",
                color: "#b22234",
                fontSize: "20px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="name"
                id="first"
                placeholder="Lupe Diaz "
                onChange={(e) => handleUpdateReps(e)}
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="position"
                id="first"
                placeholder="House Representative LD19"
                onChange={(e) => handleUpdateReps(e)}
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
                marginBottom: "10px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="email"
                id="first"
                placeholder="lupe777diaz@gmail.com"
                onChange={(e) => handleUpdateReps(e)}
              />
            </Typography>
            <button
              style={{ marginBottom: "4px" }}
              className="updateimgbtn"
              onClick={(e) => handleSubmitRep(e)}
            >
              Update Info
            </button>
          </Card>
          <Card
            className="bShadow flex"
            sx={{
              flexDirection: "column",
              alignItems: "center",
              width: "250px",
              height: "400px",
              m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${voter?.second.photo})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "90%",
                height: "75%",
                mt: "10px",
              }}
            ></Stack>
            <div className="loadimgdiv">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                name="second"
                id=""
                onChange={(e) => handleSelectedFiles(e)}
              />{" "}
            </div>
            <Typography
              sx={{
                mt: "8px",
                color: "#b22234",
                fontSize: "20px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="name"
                id="second"
                onChange={(e) => handleUpdateReps(e)}
                placeholder="Gail Griffin"
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="position"
                id="second"
                onChange={(e) => handleUpdateReps(e)}
                placeholder="House Representative LD19"
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
                marginBottom: "10px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="email"
                id="second"
                onChange={(e) => handleUpdateReps(e)}
                placeholder="griff4333@gmail.com"
              />
            </Typography>
            <button
              style={{ marginBottom: "4px" }}
              className="updateimgbtn"
              onClick={(e) => handleSubmitRep(e)}
            >
              Update Info
            </button>
          </Card>
          <Card
            className="bShadow flex"
            sx={{
              flexDirection: "column",
              alignItems: "center",
              width: "250px",
              height: "400px",
              m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${process.env.REACT_APP_SERVER}${voter?.third.photo})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "90%",
                height: "75%",
                mt: "10px",
              }}
            ></Stack>
            <div className="loadimgdiv">
              Upload Photo
              <input
                className="imguploadinput"
                type="file"
                name="third"
                id=""
                onChange={(e) => handleSelectedFiles(e)}
              />{" "}
            </div>
            <Typography
              sx={{
                mt: "8px",
                color: "#b22234",
                fontSize: "20px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="name"
                id="third"
                placeholder="David Gowan"
                onChange={(e) => handleUpdateReps(e)}
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="position"
                id="third"
                onChange={(e) => handleUpdateReps(e)}
                placeholder="Arizona Senator LD19"
              />
            </Typography>
            <Typography
              sx={{
                color: "#3c3b6e",
                fontSize: "15px",
                marginBottom: "10px",
              }}
            >
              <input
                className="vrinput"
                type="text"
                name="email"
                id="third"
                onChange={(e) => handleUpdateReps(e)}
                placeholder="gowanforaz@gmail.com"
              />
            </Typography>
            <button
              style={{ marginBottom: "4px" }}
              className="updateimgbtn"
              onClick={(e) => handleSubmitRep(e)}
            >
              Update Info
            </button>
          </Card>
        </Stack>
        <div
          style={{
            width: "100%",
            marginTop: "20px",
            marginBottom: "20px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <textarea
            style={{ width: "50%", textAlign: "center" }}
            onChange={(e) => handleUpdateContent(e)}
            name=""
            id="content2"
            rows="2"
            placeholder="Click on one of our interactive maps below to see who is
              representing you."
          ></textarea>
          <button
            id="content2"
            onClick={(e) => handleSubmitContent(e)}
            style={{
              width: "200px",
              padding: "5px",
              margin: "5px",
              fontSize: "large",
            }}
            className="btn"
          >
            Update
          </button>
        </div>
        <Stack
          className="flex"
          sx={{
            height: "auto",
            width: "100%",
            flexDirection: {
              lg: "row",
              md: "row",
              sm: "column",
              xs: "column",
            },
            justifyContent: {
              lg: "space-around",
              md: "space-around",
              sm: "center",
              xs: "center",
            },
            alignItems: "center",
          }}
        >
          <ResourceCard
            link={
              "https://cochise.maps.arcgis.com/apps/webappviewer/index.html?id=5831eb364a944560a46ca21ad04226da"
            }
            abgImg={map1}
            abuttontext={"Precinct Map"}
          />
          <ResourceCard
            link={
              "https://cochise.maps.arcgis.com/apps/webappviewer/index.html?id=5831eb364a944560a46ca21ad04226da"
            }
            abgImg={map2}
            abuttontext={"Supervisors"}
          />
          <ResourceCard
            link={
              "https://irc-az.maps.arcgis.com/apps/instant/lookup/index.html?appid=424810a4667049388ef6df4f0c73098b"
            }
            abgImg={map3}
            abuttontext={"Legislative"}
          />
          <ResourceCard
            link={
              "https://irc-az.maps.arcgis.com/apps/instant/lookup/index.html?appid=ffc3e1bac1ed4350923608ae8e3544c7"
            }
            abgImg={map4}
            abuttontext={"Congressional"}
          />
        </Stack>
        <div
          style={{
            width: "100%",
            marginTop: "20px",
            marginBottom: "20px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <textarea
            style={{ width: "50%", textAlign: "center" }}
            name="content3"
            onChange={(e) => handleUpdateContent(e)}
            id=""
            rows="1"
            placeholder="Check out the links below for more information."
          ></textarea>
          <button
            id="content3"
            onClick={(e) => handleSubmitContent(e)}
            style={{
              width: "200px",
              padding: "5px",
              margin: "5px",
              fontSize: "large",
            }}
            className="btn"
          >
            Update
          </button>
        </div>
        <Stack
          className="flex"
          sx={{
            width: "100%",
            height: "auto",
            alignItems: "center",
            justifyContent: {
              lg: "space-around",
              md: "space-around",
              sm: "center",
              xs: "center",
            },
            flexDirection: {
              lg: "row",
              md: "row",
              sm: "column",
              xs: "column",
            },
            mb: { lg: "3%", md: "5%", sm: "5%", xs: "5%" },
          }}
        >
          <ResourceCard
            link={"https://my.arizona.vote/WhereToVote.aspx?s=individual"}
            abgImg={VoterPortal}
            abuttontext={"Voter Portal"}
            text={"Access to Voter Portal"}
          />
          <Card
            className="bShadow flex"
            sx={{
              flexDirection: "column",
              width: "250px",
              height: "350px",
              m: { lg: "10px", md: "10px", sm: "10px", xs: "10px" },
            }}
          >
            <Stack
              sx={{
                backgroundImage: `url(${Liberal})`,
                backgroundSize: "cover",
                backgroundPosition: "center",
                width: "90%",
                height: "65%",
                m: "5%",
              }}
            ></Stack>
            <Stack
              className="disFlexCenter"
              sx={{
                flexDirection: "column",
              }}
            >
              <div className="loadimgdiv">
                Upload Photo
                <input className="imguploadinput" type="file" name="" id="" />
              </div>
              <Typography
                sx={{
                  textAlign: "center",
                  fontSize: "15px",
                  m: "5px",
                  color: "#3c3b6e",
                }}
              >
                <p>Liberal or Conservative?</p>
              </Typography>
            </Stack>
            <Stack
              className="disFlexCenter"
              sx={{
                flexDirection: "column",
              }}
            >
              <a
                href="http://www.liberalorconservativequiz.org/Quiz2.htm"
                target="_blank"
                style={{
                  width: "100%",
                  justifyContent: "center",
                }}
                className="atag flex"
              ></a>
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexDirection: "column",
                }}
              >
                <input
                  className="vrinput"
                  type="text"
                  onChange={(e) => handleUpdateContent(e)}
                  name="link"
                  id=""
                  placeholder="http://www.liberalorconservativequiz.org/Quiz2.htm"
                />
                <button
                  id="link"
                  onClick={(e) => handleSubmitContent(e)}
                  style={{ marginBottom: "5px", width: "100%", padding: "3px" }}
                  className="updateimgbtn"
                >
                  Update Link
                </button>
              </div>
            </Stack>
          </Card>
          <ResourceCard
            link={"https://cochise.az.gov/509/Voter-Information"}
            abgImg={VoterInfo}
            abuttontext={"Read More"}
            text={"Click below for Voter Information"}
          />
        </Stack>
      </FlagBG>
      <Footer />
    </>
  );
};

export default UpdateVoterResources;
