import { useRef, useState } from "react";
import { Box, TextField, Button } from "@mui/material";
import emailjs from "@emailjs/browser";
import Blurb from "../components/Blurb/Blurb.jsx";
import { useData } from "../hooks/context-hook.jsx";

const Contact = () => {
  const { siteData } = useData();
  let getInvolved = siteData.pages?.getInvolved;

  const form = useRef();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_5gsnz3b", "template_kryh3vc", form.current, {
        publicKey: "BJWN9qMcB5Qdys0zi",
      })
      .then(
        () => {
          console.log("SUCCESS!");
          form.current.reset();
          setName("");
          setEmail("");
          setMessage("");
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <>
      {console.log("contact", getInvolved)}
      <Blurb
        text={getInvolved?.contactUsContent}
      />
      <Box
        className="bShadow"
        sx={{
          margin: "auto",
          width: { lg: "50%", md: "50%", sm: "75%", xs: "75%" },
          textAlign: "center",
          padding: "1em",
        }}
      >
        <form ref={form} onSubmit={sendEmail}>
          <Blurb text="Your feedback is important to us. Share your ideas, suggestions and events to help us better serve our community." />
          <TextField
            className="input"
            label="Name"
            name="name"
            variant="outlined"
            margin="normal"
            required
            fullWidth
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            className="input"
            label="Email"
            name="email"
            type="email"
            variant="outlined"
            margin="normal"
            required
            fullWidth
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            className="input"
            label="Message"
            name="message"
            multiline
            rows={4}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            sx={{
              background: "white",
              marginBottom: "20px",
            }}
          />
          <Button
            className="btn"
            type="submit"
            variant="contained"
            fullWidth
            style={{ marginTop: "1em", padding: "1em" }}
          >
            Send
          </Button>
        </form>
      </Box>
    </>
  );
};

export default Contact;
