import { useRef, useState } from "react";
import { Box, TextField, Button } from "@mui/material";
import emailjs from "@emailjs/browser";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import Blurb from "../../components/Blurb/Blurb";
import Title from "../../components/Title/Title";
import FlagBG from "../../components/FlagBG/FlagBG";
import axios from "axios";

import { useData } from "../../hooks/context-hook.jsx";

const UpdateContact = () => {
  const { siteData, updateAbout } = useData();
  const contact = siteData.pages?.getInvolved;
  const [content, setContent] = useState("");
  const handleSubmit = (e) => {
    axios.post(`${process.env.REACT_APP_SERVER}/admin/update/contact/content`, {
      content: content,
    });
  };
  const handleContentChange = (e) => {
    setContent(e.target.value);
  };
  const form = useRef();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_5gsnz3b", "template_kryh3vc", form.current, {
        publicKey: "BJWN9qMcB5Qdys0zi",
      })
      .then(
        () => {
          console.log("SUCCESS!");
          form.current.reset();
          setName("");
          setEmail("");
          setMessage("");
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <>
      {console.log("content", content)}
      <NavbarAdmin />
      <FlagBG>
        <Title text="Contact Us" />
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            marginTop: "10px",
          }}
        >
          <textarea
            style={{ width: "50%", textAlign: "center" }}
            name=""
            id=""
            rows="3"
            placeholder={contact?.contactUsContent}
            onChange={(e) => handleContentChange(e)}
          ></textarea>
          <Button
            variant="contained"
            className="btn"
            sx={{ m: "1em !important", width: "15%" }}
            onClick={(e) => handleSubmit(e)}
            id=""
          >
            Update
          </Button>
        </div>
        <Box
          className="bShadow"
          sx={{
            backgroundColor: "#fff",
            width: "90%",
            margin: "auto",
            maxWidth: "800px",
            textAlign: "center",
            padding: "1em",
          }}
        >
          <form ref={form} onSubmit={sendEmail}>
            <Blurb text="Your feedback is important to us. Share your ideas and suggestions to help us better serve our community." />
            <TextField
              className="input"
              label="Name"
              name="name"
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              className="input"
              label="Email"
              name="email"
              type="email"
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <TextField
              className="input"
              label="Message"
              name="message"
              multiline
              rows={4}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              sx={{
                background: "white",
              }}
            />
            <Button
              className="btn"
              type="submit"
              variant="contained"
              fullWidth
              style={{ marginTop: "1em", padding: "1em" }}
            >
              Send
            </Button>
          </form>
        </Box>
      </FlagBG>
      <Footer />
    </>
  );
};

export default UpdateContact;
