import React, { useState } from "react";
import { Box, Stack, Typography, Button } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import LoginModal from "../LoginModal/LoginModal";
import MenuModal from "../Menu Modal/MenuModal";
import Logo from "../../assets/images/CCCLogo.png";
import NavLinks from "../NavBarLinks/NavbarLinks";

const links = [
  { to: "/", title: "Home" },
  { to: "/About", title: "About" },
  { to: "/PCResources", title: "Resources" },
  { to: "/VoterResources", title: "Voter Resources" },
  { to: "/GetInvolved", title: "Get Involved" },
  { to: "/Contact", title: "Contact" },
];

const Navbar = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [loginModalOpen, setLoginModalOpen] = useState(false);

  const toggleMenuModal = () => {
    setMenuOpen((prev) => !prev);
  };

  const toggleLoginModal = () => {
    setLoginModalOpen((prev) => !prev);
  };

  const closeLoginModal = () => {
    setLoginModalOpen(false);
  };

  return (
    <>
      {loginModalOpen && <LoginModal handleClose={closeLoginModal} />}
      <Box
        sx={{
          display: "flex",
          width: "100%",
          height: "65px",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "0.5em",
        }}
      >
        <Stack
          sx={{
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              width: "60px",
              height: "60px",
              borderRadius: "50%",
              backgroundImage: `url(${Logo})`,
              backgroundSize: "contain",
              backgroundRepeat: "no-repeat",
              marginRight: "10px",
            }}
          ></Box>
          <Stack>
            <Typography sx={{ fontSize: "16px", color: "grey" }}>
              Cochise County
            </Typography>
            <Typography sx={{ fontSize: "16px", color: "grey" }}>
              Conservatives
            </Typography>
          </Stack>
        </Stack>
        <Stack
          sx={{
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-evenly",
            display: { xs: "none", md: "flex" },
            width: { lg: "75%", md: "75%", sm: "50%", xs: "50%" },
          }}
        >
          <NavLinks links={links} />
          <Typography
            sx={{ cursor: "pointer", color: "grey" }}
            onClick={toggleLoginModal}
          >
            Admin
          </Typography>
          {/* <a
            href="https://secure.anedot.com/ccrc/donate"
            target="_blank"
            rel="noreferrer"
            style={{ textDecoration: "none", marginLeft: "10px" }}
          >
            <Button
              variant="contained"
              className="btn"
              sx={{ borderRadius: "20px !important" }}
            >
              Donate
            </Button>
          </a> */}
        </Stack>
        <MenuIcon
          sx={{
            display: { xs: "block", md: "none" },
            color: "grey",
            fontSize: "40px",
          }}
          onClick={toggleMenuModal}
        />
      </Box>
      <MenuModal open={menuOpen} handleClose={toggleMenuModal} />
    </>
  );
};

export default Navbar;
