import { Stack } from "@mui/material";
import Blurb from "../components/Blurb/Blurb.jsx";
import ResourceCard from "../components/Cards/ResourceCard.jsx";
import { useData } from "../hooks/context-hook";

const PCresourcesContent = () => {
  const { siteData } = useData();
  const pcResources = siteData.pages.resources;

  return (
    <>
      {siteData?.pages.resources ? (
        <>
          <Blurb text={pcResources?.content} />
          <Stack
            className="disFlexCenter"
            sx={{
              flexDirection: { lg: "row" },
              height: "auto",
            }}
          >
            <ResourceCard
              link={`${process.env.REACT_APP_SERVER}${pcResources.pdf1.file}`}
              pdfbgImg={`${process.env.REACT_APP_SERVER}${pcResources.pdf1.img}`}
              pdfbuttontext={siteData.pages.resources.pdf1.name}
            />
            <ResourceCard
              link={`${process.env.REACT_APP_SERVER}${pcResources.pdf2.file}`}
              pdfbgImg={`${process.env.REACT_APP_SERVER}${pcResources.pdf2.img}`}
              pdfbuttontext={siteData.pages.resources.pdf2.name}
            />
            <ResourceCard
              link={`${process.env.REACT_APP_SERVER}${pcResources.pdf3.file}`}
              pdfbgImg={`${process.env.REACT_APP_SERVER}${pcResources.pdf3.img}`}
              pdfbuttontext={siteData.pages.resources.pdf3.name}
            />
            <ResourceCard
              link={`${process.env.REACT_APP_SERVER}${pcResources.pdf4.file}`}
              pdfbgImg={`${process.env.REACT_APP_SERVER}${pcResources.pdf4.img}`}
              pdfbuttontext={siteData.pages.resources.pdf4.name}
            />
            <ResourceCard
              link={`${process.env.REACT_APP_SERVER}${pcResources.pdf5.file}`}
              pdfbgImg={`${process.env.REACT_APP_SERVER}${pcResources.pdf5.img}`}
              pdfbuttontext={siteData.pages.resources.pdf5.name}
            />
          </Stack>
        </>
      ) : (
        <p>Loading</p>
      )}
    </>
  );
};

export default PCresourcesContent;
