import { useRef, useState } from "react";
import { Box, Button, TextField } from "@mui/material";
import emailjs from "@emailjs/browser";
import Blurb from "../../components/Blurb/Blurb.jsx";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import Title from "../../components/Title/Title.jsx";
import FlagBG from "../../components/FlagBG/FlagBG.jsx";

const UpdateSubscribe = () => {
  const form = useRef();
  const [email, setEmail] = useState("");

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_5gsnz3b", "template_pytmelq", form.current, {
        publicKey: "BJWN9qMcB5Qdys0zi",
      })
      .then(
        () => {
          form.current.reset();
          setEmail("");
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <>
      <NavbarAdmin />
      <FlagBG>
        <Title text="Stay Connected" />
        <Box
          className="bShadow"
          sx={{
            backgroundColor: "white",
            margin: {
              lg: "15% auto",
              md: "15% auto",
              sm: "15% auto",
              xs: "45% auto",
            },
            width: { lg: "50%", md: "50%", sm: "50%", xs: "75%" },
            textAlign: "center",
            padding: "1em",
          }}
        >
          <form
            className="flex"
            ref={form}
            onSubmit={sendEmail}
            style={{
              backgroundColor: "white",
              flexDirection: "column",
              width: "100%",
              padding: "1em",
            }}
          >
            <Blurb text="Stay Informed with the Cochise County Conservative Newsletter! Subscribe today and be a part of our growing network of engaged citizens!" />
            <TextField
              className="input"
              label="Email"
              name="email"
              type="email"
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <Button
              className="btn"
              type="submit"
              variant="contained"
              fullWidth
              style={{ marginTop: "1em", padding: "1em" }}
            >
              Submit
            </Button>
          </form>
        </Box>
      </FlagBG>
      <Footer />
    </>
  );
};

export default UpdateSubscribe;
