import {
  Box,
  CardActions,
  Collapse,
  Card,
  CardContent,
  Typography,
  CardMedia,
  Button,
} from "@mui/material";
import { useState } from "react";

const BlogCard = ({
  title,
  author,
  blogPosts,
  date,
  imageUrl,
  postId,
  onDelete,
}) => {
  const [expanded, setExpanded] = useState(false);

  const expandedHandler = () => {
    setExpanded((prev) => !prev);
  };

  const handleDelete = () => {
    fetch(`${process.env.REACT_APP_SERVER}/api/blogPosts/${postId}`, {
      method: "DELETE",
    })
      .then(() => onDelete(postId))
      .catch((err) => console.error(err));
  };

  return (
    <>
      <Card sx={{ marginBottom: "1rem" }}>
        {imageUrl && (
          <CardMedia
            component="img"
            height="140"
            image={`${process.env.REACT_APP_SERVER}${imageUrl}`}
            alt={title}
          />
        )}
        <CardContent>
          <Typography variant="h5" component="div">
            {title}
          </Typography>
          <Typography sx={{ mb: 1.5 }} color="text.secondary">
            {author} - {new Date(date).toLocaleDateString()}
          </Typography>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <Typography variant="body2">{blogPosts}</Typography>
          </Collapse>
          {onDelete && (
            <Button
              variant="contained"
              color="secondary"
              onClick={handleDelete}
            >
              Delete
            </Button>
          )}
        </CardContent>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            mt: "1rem",
          }}
        >
          <CardActions>
            <Button onClick={expandedHandler} size="small">
              {expanded ? "Read Less" : "Read More"}
            </Button>
          </CardActions>
        </Box>
      </Card>
    </>
  );
};

export default BlogCard;
