import { Box, Typography } from "@mui/material";

const Blurb = ({ text }) => {
  return (
    <Box sx={{ padding: "2% 5%" }}>
      <Typography
        className="blurb"
        variant="body1"
        sx={{
          fontSize: { lg: "18px", md: "16px", sm: "15px", xs: "14px" },
        }}
      >
        {text}
      </Typography>
    </Box>
  );
};

export default Blurb;
