import { Box } from "@mui/material";
import Slider from "../components/Slider/Slider";
import IconCard from "../components/Cards/IconCard";
import EventNoteIcon from "@mui/icons-material/EventNote";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import MarkunreadMailboxOutlinedIcon from "@mui/icons-material/MarkunreadMailboxOutlined";

const HomePage = () => {
  return (
    <>
      <Slider />
      <Box
        className="disFlexSpace"
        sx={{
          flexDirection: { lg: "row", md: "row", sm: "column", xs: "column" },
          width: "100%",
          height: "auto",
        }}
      >
        <IconCard
          icon={<EventNoteIcon className="cardIcon" />}
          title={"Events Calendar"}
          text={"Check out our Events Calendar for updates on local events."}
          navlink={"/Events"}
          navbuttontext={"See Events"}
        />

        <IconCard
          icon={<MarkunreadMailboxOutlinedIcon className="cardIcon" />}
          title={"Stay Informed"}
          text={"Subscribe to our e-mail list to receive important updates."}
          navlink={"/Subscribe"}
          navbuttontext={"Subscribe Now"}
        />

        <IconCard
          icon={<AttachMoneyIcon className="cardIcon" />}
          title={"Donate"}
          text={"Campaigns are costly. Every bit of support helps."}
          // alink={"https://secure.anedot.com/ccrc/donate"}
          abuttontext={"Donate Now"}
        />
      </Box>
    </>
  );
};

export default HomePage;
