import React from "react";
import { useData } from "../../hooks/context-hook";
import { Swiper, SwiperSlide, useSwiper } from "swiper/react";
import { sliderSettings } from "../../Utility/common";
import "swiper/css";

const Slider = () => {
  const { siteData } = useData();
  const slide = siteData.pages.slide;

  return (
    <>
      {console.log("slide", slide)}
      <div className="swiperdiv">
        <Swiper {...sliderSettings}>
          {slide &&
            slide.map((photo, i) => (
              <SwiperSlide key={i}>
                <div
                  className="imgdiv1"
                  style={{ display: "flex", flexDirection: "column" }}
                >
                  <img
                    src={`${process.env.REACT_APP_SERVER}${photo.image}`}
                    alt="pics"
                    className="sliderimg"
                  />
                </div>
              </SwiperSlide>
            ))}
          <SliderButtons className="sliderbtns" />
        </Swiper>
      </div>
    </>
  );
};

export default Slider;

const SliderButtons = () => {
  const swiper = useSwiper();
  return (
    <div className="sliderbtnsdiv">
      <button onClick={() => swiper.slidePrev()} className="lbtn sliderbtns">
        &lt;
      </button>
      <button onClick={() => swiper.slideNext()} className="rbtn sliderbtns">
        &gt;
      </button>
    </div>
  );
};
