import React, { useState, useEffect } from "react";
import axios from "axios";
import { useData } from "../../hooks/context-hook";
import { Swiper, SwiperSlide, useSwiper } from "swiper/react";
import { sliderSettings } from "../../Utility/common";
import imgupload from "../../assets/images/imgupload.jpg";

import "swiper/css";

const Slider2 = () => {
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [updateSelectedFiles, setUpdateSelectedFiles] = useState([]);
  const { siteData, updateAbout } = useData();

  const slides = siteData.pages.slide;
  const [updateID, setUpdateID] = useState(null);
  let slidePage = slides;

  const [reRender, setReRender] = useState(false);

  useEffect(() => {
    console.warn("useEffect get page");
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_SERVER}/api/getSite`,
    })
      .then((res) => {
        updateAbout(res.data);
      })
      .catch((err) => console.log("err", err));
  }, [reRender]);

  useEffect(() => {
    if (selectedFiles.length > 0) {
      handleImageSubmit();
    }
  }, [selectedFiles]);

  useEffect(() => {
    if (updateSelectedFiles.length > 0) {
      updatePhoto();
    }
  }, [updateSelectedFiles]);

  const handleSelectedFiles = (e) => {
    console.log("adding a file", e.target.files);
    setSelectedFiles(e.target.files);
  };

  const handleImageSubmit = () => {
    const formData = new FormData();
    formData.append("images", selectedFiles[0]);

    axios({
      method: "POST",
      withCredentials: true,
      data: formData,
      headers: { "Content-Type": "multipart/form-data" },
      url: `${process.env.REACT_APP_SERVER}/admin/updates/slideShow/image/home`,
    })
      .then((res) => {
        alert(res.data.msg);

        setReRender((prev) => !prev); // Toggle reRender state to trigger re-render

        setSelectedFiles([]);
      })
      .catch((err) => console.log("err", err));
  };

  const deletePhoto = (id) => {
    console.warn("slides", slides, "id", id);
    let finder = slides[id];
    console.log("finder", finder);
    let mongoId = finder._id;

    axios({
      method: "DELETE",
      withCredentials: true,
      url: `${process.env.REACT_APP_SERVER}/admin/updates/slideShow/image/home/delete/${mongoId}`,
    })
      .then((res) => {
        slidePage = res.data.slide;
        setReRender((prev) => !prev); // Toggle reRender state to trigger re-render

        alert("Image Deleted Successfully ");
      })
      .catch((err) => console.log("err", err));
  };

  const handleUpdateFile = (e, id) => {
    setUpdateSelectedFiles(e.target.files);
    setUpdateID(id);
  };

  const updatePhoto = () => {
    if (updateSelectedFiles.length) {
      let mongoId = slides[updateID]._id;
      const formData = new FormData();
      formData.append("images", updateSelectedFiles[0]);

      axios({
        method: "POST",
        withCredentials: true,
        data: formData,
        headers: { "Content-Type": "multipart/form-data" },
        url: `${process.env.REACT_APP_SERVER}/admin/updates/slideShow/image/home/updateImg/${mongoId}`,
      })
        .then((res) => {
          slidePage = res.data.slide;
          setReRender((prev) => !prev); // Toggle reRender state to trigger re-render
          alert("Image Updated Successfully ");
        })
        .catch((err) => console.log("err", err));
    }
  };

  return (
    <>
      <div className="swiperdiv">
        <Swiper {...sliderSettings}>
          {slides &&
            slidePage.map((photo, i) => (
              <SwiperSlide key={i}>
                <div
                  className="imgdiv1"
                  style={{ display: "flex", flexDirection: "column" }}
                >
                  <img
                    src={`${process.env.REACT_APP_SERVER}${photo.image}`}
                    alt="pics"
                    className="sliderimg"
                  />
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                  }}
                >
                  <div className="loadimgdiv3">
                    UPDATE PHOTO
                    <input
                      id={i}
                      type="file"
                      onChange={(e) => handleUpdateFile(e, i)}
                      className="imguploadinput"
                    />
                  </div>
                  <button
                    style={{
                      outline: "none",
                      border: "none",
                    }}
                    className="loadimgdiv3"
                    onClick={() => deletePhoto(i)}
                  >
                    Delete
                  </button>
                </div>
              </SwiperSlide>
            ))}

          <SliderButtons className="sliderbtns" />
        </Swiper>
      </div>
      <div
        className="imgdiv1"
        style={{ display: "flex", flexDirection: "column" }}
      >
        <img src={imgupload} alt="pics" className="sliderimg1" />
        <div
          className="loadimgdiv3"
          style={{
            width: "200px",
            margin: "10px",
          }}
        >
          ADD IMAGE
          <input
            className="imguploadinput1"
            type="file"
            onChange={(e) => handleSelectedFiles(e)}
          ></input>
        </div>
      </div>
    </>
  );
};

export default Slider2;

const SliderButtons = () => {
  const swiper = useSwiper();
  return (
    <div className="sliderbtnsdiv">
      <button onClick={() => swiper.slidePrev()} className="lbtn sliderbtns">
        &lt;
      </button>
      <button onClick={() => swiper.slideNext()} className="rbtn sliderbtns">
        &gt;
      </button>
    </div>
  );
};
