import React, { useState, useEffect } from "react";
import axios from "axios";
import { Typography, Stack, Card, Button } from "@mui/material";
import AZflag from "../../assets/images/AZflag.jpg";
import { useData } from "../../hooks/context-hook";
import NavbarAdmin from "../../components/Navbar/NavbarAdmin";
import Footer from "../../components/Footer/Footer";
import Title from "../../components/Title/Title.jsx";
import FlagBG from "../../components/FlagBG/FlagBG.jsx";

const UpdateAboutContent = () => {
  const [selectedFiles, setSelectedFiles] = useState([]);

  const [imageRoute, setImageRoute] = useState("");

  const handleSelectedFiles = (e) => {
    setImageRoute(e.target.name);
    setSelectedFiles(e.target.files);
  };

  const [render, setRender] = useState(1);

  useEffect(() => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_SERVER}/api/getSite`,
    })
      .then((res) => {
        updateAbout(res.data);
      })
      .catch((err) => console.log("err", err));
  }, [render]);

  useEffect(() => {
    handleImageSubmit(imageRoute);
  }, [selectedFiles]);

  const { siteData, updateAbout } = useData();
  const about = siteData.pages.about;
  const [imageUrls, setImageUrls] = useState({});
  const [update, setUpdate] = useState({});

  const handleChange = (e) => {
    setUpdate((prev) => ({
      [e.target.name]: e.target.value,
    }));
  };

  const handleImageSubmit = (imageRoute) => {
    const formData = new FormData();
    formData.append("what", imageRoute);
    formData.append("images", selectedFiles[0]);

    axios({
      method: "POST",
      withCredentials: true,
      data: formData,
      headers: { "Content-Type": "multipart/form-data" },
      url: `${process.env.REACT_APP_SERVER}/admin/updates/image/${imageRoute}/  `,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })
      .catch((err) => console.log("err", err));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    axios({
      method: "POST",
      withCredentials: true,
      data: update,
      url: `${process.env.REACT_APP_SERVER}/admin/updates/about/65e71cca4d92d3959da05dda`,
    })
      .then((res) => {
        alert(res.data.msg);
        setRender(render + 1);
      })

      .catch(() => alert("update failed"));
  };

  return (
    <>
      <NavbarAdmin />
      {siteData?.pages?.about ? (
        <FlagBG>
          <Title text="About Us" />
          <Stack
            sx={{
              width: "100%",
            }}
          >
            <Typography
              sx={{
                textAlign: "center",
                fontSize: { lg: "18px", md: "16px", sm: "15px", xs: "14px" },
                color: "#fff",
                m: "20px",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <textarea
                  style={{
                    width: "50%",
                    textAlign: "center",
                    padding: "5px",
                    margin: "5px",
                    padding: "10px",
                  }}
                  rows="6"
                  onChange={(e) => handleChange(e)}
                  id="about"
                  name="content"
                  placeholder={about.content}
                ></textarea>

                <Button
                  variant="contained"
                  className="btn"
                  sx={{ m: "1em !important", width: "15%" }}
                  onClick={(e) => handleSubmit(e)}
                  id="about"
                >
                  Update
                </Button>
              </div>
            </Typography>
          </Stack>
          <Stack
            className="disFlexCenter"
            sx={{
              flexDirection: {
                lg: "row",
                md: "row",
                sm: "column",
                xs: "column",
              },
              width: "100%",
              height: "auto",
            }}
          >
            <Card
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                padding: "1em",
                width: "225px",
                height: "325px",
                m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
              }}
            >
              <Stack
                sx={{
                  backgroundImage: `url(${process.env.REACT_APP_SERVER}${about.chairman.img}), url(${AZflag})`,
                  backgroundSize: "cover",
                  backgroundPosition: "center",
                  width: "100%",
                  height: "75%",
                }}
              ></Stack>
              <div className="loadimgdiv btn">
                Upload Photo
                <input
                  className="imguploadinput"
                  type="file"
                  name="chairman"
                  onChange={(e) => handleSelectedFiles(e)}
                ></input>
              </div>
              <Typography
                sx={{
                  color: "#3c3b6e",
                  fontSize: "15px",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <input
                    className="abouttextinput"
                    id="about"
                    name="chairman"
                    onChange={(e) => handleChange(e)}
                    placeholder={about.chairman.name}
                  ></input>

                  <button className="btn1" onClick={(e) => handleSubmit(e)}>
                    Update
                  </button>
                </div>
              </Typography>
              <Typography
                sx={{
                  color: "#3c3b6e",
                  fontSize: "20px",
                }}
              >
                <p>Founder</p>
              </Typography>
            </Card>

            <Card
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                padding: "1em",
                width: "225px",
                height: "325px",
                m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
              }}
            >
              <Stack
                sx={{
                  backgroundImage: `url(${process.env.REACT_APP_SERVER}${about.firstViceChair.img}), url(${AZflag})`,
                  backgroundSize: "cover",
                  backgroundPosition: "center",
                  width: "100%",
                  height: "75%",
                }}
              ></Stack>
              <div className="loadimgdiv btn">
                Upload Photo
                <input
                  className="imguploadinput"
                  type="file"
                  name="firstViceChair"
                  onChange={(e) => handleSelectedFiles(e)}
                ></input>
              </div>

              <Typography
                sx={{
                  color: "#3c3b6e",
                  fontSize: "15px",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <input
                    className="abouttextinput"
                    id="about"
                    name="firstViceChair"
                    onChange={(e) => handleChange(e)}
                    placeholder={about.firstViceChair.name}
                  ></input>

                  <button className="btn1" onClick={(e) => handleSubmit(e)}>
                    Update
                  </button>
                </div>
              </Typography>

              <Typography
                sx={{
                  color: "#3c3b6e",
                  fontSize: "20px",
                }}
              >
                <p>Founder</p>
              </Typography>
            </Card>
          </Stack>
        </FlagBG>
      ) : (
        <p>loading</p>
      )}
      <Footer />
    </>
  );
};

export default UpdateAboutContent;
