import React from "react";
import { Stack, Typography, Card, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";

// Icon Card with link to pages within the app
const IconCard = ({
  icon,
  title,
  text,
  navlink,
  alink,
  navbuttontext,
  abuttontext,
  pdfbuttontext,
  pdflink,
}) => {
  const nav = useNavigate();
  return (
    <>
      <Card
        className="bShadow flex"
        sx={{
          flexDirection: "column",
          width: "250px",
          height: "300px",
          m: { lg: "1em", md: "10px", sm: "10px", xs: "10px" },
        }}
      >
        <Stack
          className="disFlexCenter"
          sx={{
            width: "100%",
            height: "50%",
            backgroundColor: "#b22234",
          }}
        >
          <Stack className="icon">{icon}</Stack>
        </Stack>
        <Stack
          className="disFlexCenter"
          sx={{
            flexDirection: "column",
          }}
        >
          <Typography sx={{ m: "5px" }}>
            <h4>{title}</h4>
          </Typography>
          <Typography sx={{ textAlign: "center", fontSize: "15px", m: "5px" }}>
            <p>{text}</p>
          </Typography>

          {navbuttontext && (
            <Button onClick={() => nav(navlink)} className="btn lgBtn">
              {navbuttontext}
            </Button>
          )}
          {abuttontext && (
            <a
              href={alink}
              target="_blank"
              style={{
                width: "100%",
                justifyContent: "center",
              }}
              className="atag flex"
            >
              <Button className="btn lgBtn">{abuttontext}</Button>
            </a>
          )}
          {pdfbuttontext && (
            <Button onClick={() => window.open(pdflink)} className="btn lgBtn">
              {pdfbuttontext}
            </Button>
          )}
        </Stack>
      </Card>
    </>
  );
};

export default IconCard;
