import { useRef, useState } from "react";
import { Box, Button, TextField } from "@mui/material";
import emailjs from "@emailjs/browser";
import Blurb from "../components/Blurb/Blurb.jsx";

const Subscribe = () => {
  const form = useRef();
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_5gsnz3b", "template_pytmelq", form.current, {
        publicKey: "BJWN9qMcB5Qdys0zi",
      })
      .then(
        () => {
          console.log("SUCCESS!");
          form.current.reset();
          setEmail("");
          setName("");
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <>
      <Box sx={{ marginBottom: { sm: "5em" } }}>
        <Blurb
          text="Join our community and stay updated with the latest news, events, and initiatives from the Cochise County Conservatives. By subscribing to our newsletter, youll receive
        Exclusive Updates, Legislative News, Volunteer Opportunities, Campaign Insights, Community Stories and much more."
        />
        <Box
          className="bShadow"
          sx={{
            margin: "auto",
            width: { lg: "50%", md: "50%", sm: "75%", xs: "75%" },
            height: "25%",
            textAlign: "center",
            padding: "1em",
          }}
        >
          <Blurb text="Stay Informed with the Cochise County Conservative Newsletter! Subscribe today and be a part of our growing network of engaged citizens!" />
          <form
            className="flex"
            ref={form}
            onSubmit={sendEmail}
            style={{
              flexDirection: "column",
              width: "100%",
              padding: ".5em",
            }}
          >
            <TextField
              className="input"
              label="Name"
              name="name"
              type="text"
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              className="input"
              label="Email"
              name="email"
              type="email"
              variant="outlined"
              margin="normal"
              required
              fullWidth
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <Button
              className="btn"
              type="submit"
              variant="contained"
              fullWidth
              style={{ marginTop: "1em", padding: "1em" }}
            >
              Submit
            </Button>
          </form>
        </Box>
      </Box>
    </>
  );
};

export default Subscribe;
