import React, { useState } from "react";
import { Box, Stack, Typography } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import LoginModal from "../LoginModal/LoginModal";
import Logo from "../../assets/images/CCCLogo.png";
import NavbarLinks from "../NavBarLinks/NavbarLinks";
import AdminMenuModal from "../Menu Modal/AdminMenuModal";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const links = [
  { to: "/admin/UpdateHomePage", title: "Home" },
  { to: "/admin/updateAbout", title: "About" },
  { to: "/admin/updatePCResources", title: "Resources" },
  { to: "/admin/UpdateVoterResources", title: "Voter Resources" },
  { to: "/admin/UpdateGetInvolved", title: "Get Involved" },
  { to: "/admin/UpdateEvents", title: "Events" },
  { to: "/admin/UpdateSubscribe", title: "Subscribe" },
  { to: "/admin/UpdateContact", title: "Contact" },
];

const NavbarAdmin = () => {
  let nav = useNavigate();

  const [modal, setModal] = useState(false);
  const [modal1, setModal1] = useState(false);

  const handleMenuModal = () => {
    setModal((prev) => !prev);
  };

  const handleLoginModal = () => {
    setModal1((prev) => !prev);
  };

  const handleClose = () => {
    setModal1((prev) => !prev);
  };

  const handleLogout = (e) => {
    axios({
      method: "get",
      url: "http://localhost:5000/api/logout",
      withCredentials: true,
    })
      .then((res) => {
        nav("/");
      })
      .catch((err) => console.log("logouterr", err));
  };

  return (
    <>
      {modal1 ? <LoginModal handleClose={handleClose} /> : null}
      <Box
        className="flex"
        sx={{
          width: "100%",
          height: "65px",
          backgroundColor: "hsla(353, 68%, 42%, 0.214)",
          justifyContent: {
            lg: "space-between",
            md: "space-between",
            sm: "space-between",
            xs: "none",
          },
          padding: ".5em",
        }}
      >
        <Stack
          className="flex"
          sx={{
            flexDirection: "row",
            alignItems: "center",
            width: { lg: "25%", md: "25%", sm: "50%", xs: "50%" },
          }}
        >
          <Stack
            sx={{
              width: "60px",
              height: "60px",
              borderRadius: "50%",
              backgroundImage: `url(${Logo})`,
              backgroundSize: "contain",
              backgroundRepeat: "no-repeat",
            }}
          ></Stack>
          <Stack className="navFont">
            <Typography
              sx={{
                fontSize: { lg: "16px", md: "14px", sm: "12px", xs: "12px" },
              }}
            >
              <p>Cochise County</p>
            </Typography>
            <Typography
              sx={{
                fontSize: { lg: "16px", md: "14px", sm: "12px", xs: "12px" },
              }}
            >
              <p>Conservatives</p>
            </Typography>
          </Stack>
        </Stack>
        <Stack
          className="disFlexSpace"
          sx={{
            flexDirection: "row",
            width: { lg: "75%", md: "75%", sm: "50%", xs: "50%" },
          }}
        >
          <NavbarLinks links={links} />
          <Typography
            sx={{
              display: { lg: "block", md: "block", sm: "none", xs: "none" },
            }}
            className="headertext"
            onClick={() => handleLogout()}
          >
            <p className="navFont">Logout</p>
          </Typography>
          {modal ? <AdminMenuModal /> : null}
          <MenuIcon
            sx={{
              display: { lg: "none", md: "none", sm: "block", xs: "block" },
              fontSize: "40px",
              color: "grey",
            }}
            onClick={() => handleMenuModal()}
          />
        </Stack>
      </Box>
    </>
  );
};

export default NavbarAdmin;
