import React from "react";
import { Card, Stack, Typography } from "@mui/material";
import AZflag from "../../assets/images/AZflag.jpg";

// Committee Chairholders and Reprsentatives Card
const Card2 = ({ bgimg, title, name, email }) => {
  return (
    <Card
      className="bShadow flex"
      sx={{
        flexDirection: "column",
        alignItems: "center",
        width: "250px",
        height: "300px",
        m: { lg: "10px", md: "10px", sm: "10px", xs: "5px" },
      }}
    >
      <Stack
        sx={{
          backgroundImage: `url(${bgimg ?? AZflag})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          width: "90%",
          height: "75%",
          mt: "10px",
        }}
      ></Stack>
      <Typography
        sx={{
          mt: "8px",
          color: "#b22234",
          fontSize: "20px",
        }}
      >
        <p>{name}</p>
      </Typography>
      <Typography
        sx={{
          color: "#3c3b6e",
          fontSize: "15px",
        }}
      >
        <p>{title}</p>
      </Typography>
      <Typography
        sx={{
          color: "#3c3b6e",
          fontSize: "15px",
          marginBottom: "10px",
        }}
      >
        <p>{email}</p>
      </Typography>
    </Card>
  );
};

export default Card2;
