import { Typography, Box } from "@mui/material";

const Title = ({ text }) => {
  return (
    <Box>
          <Typography
            className="flex title"
            variant="h1"
            sx={{
              fontSize: { lg: "40px", md: "35px", sm: "25px", xs: "20px" },
              justifyContent: "center",
              textAlign: "center",
              margin: "1em",
            }}
          >
            {text}
          </Typography>
        </Box>
  );
};

export default Title;
