import { Typography, Box } from "@mui/material";
import RepsCard from "../components/Cards/RepsCard";
import FounderCard from "../components/Cards/FounderCards";
import { useData } from "../hooks/context-hook";
import React  from "react";

const About = () => {
  const { siteData } = useData();
  const about = siteData.pages.about;
  return (
    <>
      {siteData?.pages.about ? (
        <>
          <Box sx={{ padding: "2% 5%" }}>
            <Typography
              className="blurb"
              variant="body1"
              sx={{
                textAlign: "center",
                fontSize: { lg: "18px", md: "16px", sm: "15px", xs: "14px" },
              }}
            >
              <p>{about.content}</p>
            </Typography>
          </Box>
          <Box
            className="disFlexSpace"
            sx={{
              flexDirection: {
                lg: "row",
                md: "row",
                sm: "column",
                xs: "column",
              },
              width: "100%",
              height: "auto",
            }}
          >
            <FounderCard
              bgimg={`${process.env.REACT_APP_SERVER}${about.chairman.img}`}
              name={about.chairman.name}
              title={"Founder"}
              blurb={"As a dedicated mother of three and proud grandmother of two, I have built my life around family and community. My journey in public service has seen me wear many hats, Volunteer Board Member, Community Event Organizer, State Delegate and State Committeeman to Former County Chairman. As a Constitutional Conservative, I have been committed to preserving our values and principles. Through training with Turning Point, I have honed my skills in networking with conservative organizations, aiming to transform our state and counties into strongholds of conservative thought. My goal is to educate and empower our citizens through training and inspirational speakers, ensuring our community remains vibrant and resilient. Together, we will work tirelessly to uphold the Constitution, protect individual liberties, and promote conservative ideals that will strengthen our community for generations to come."}
            />
            <FounderCard
              bgimg={`${process.env.REACT_APP_SERVER}${about.firstViceChair.img}`}
              name={about.firstViceChair.name}
              title={"Founder"}
              blurb={"District Chairman, State Committeeman, Precinct Committeeman for Pima County, and Arizona Republican Party CD6 Member at Large. I am honored to serve you in these capacities, working diligently to uphold the principles of life, liberty, and the pursuit of happiness while protecting the Constitution of the United States of America and the State of Arizona.As a committed leader, I am actively engaged in our local Southern Arizona precinct communities, representing Pima, Cochise, Graham, Greenlee, and Santa Cruz counties.  My dedication to the Republican Precinct Committeemen is unwavering, providing opportunities for community engagement and offering excellent educational programs to build skills and knowledge. By upholding our Republican values and supporting strong conservative Republican candidates, I strive to be the best representation of our party and its foundational principles."}
            />
          </Box>
        </>
      ) : (
        <p>loading </p>
      )}
    </>
  );
};

export default About;
