import React from "react";
import { Card, Grid, Stack, Typography, Box } from "@mui/material";
import AZflag from "../../assets/images/AZflag.jpg";

// Founders Of The Organization Card
const FounderCard = ({ bgimg, title, name, email, blurb }) => {
  return (
    <Grid container spacing={1} justifyContent="center">
      <Card
        className="bShadow flex"
        sx={{
          display: "flex",
          flexDirection: { xs: "column", md: "row" },
          alignItems: "center",
          width: "100%",
          maxWidth: "800px",
          height: "auto",
          m: { lg: "10px", md: "10px", sm: "20px", xs: "20px" },
          p: "1em",
        }}
      >
        <Stack
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            width: { xs: "100%", md: "30%" },
            height: "auto",
          }}
        >
          <Box
            sx={{
              backgroundImage: `url(${bgimg ?? AZflag})`,
              backgroundSize: "cover",
              backgroundPosition: "center",
              width: "100%",
              height: "250px",   
              borderRadius: "10px",
            }}
          />
         <Typography
        sx={{
          mt: "8px",
          color: "#b22234",
          fontSize: "20px",
          textAlign: "center",
        }}
      >
        {name}
      </Typography>
        </Stack>
        <Box
          sx={{
            width: { xs: "100%", md: "70%" },
            p: "10px",
          }}
        >
          <Typography
            sx={{
              color: "#3c3b6e",
              fontSize: "15px",
              textAlign: "center",
            }}
          >
            {blurb}
          </Typography>
        </Box>
      </Card>
    </Grid>
  );
};

export default FounderCard;
