import { Stack, Typography } from "@mui/material";

const Header = ({ text, bgimg }) => {
  return (
    <Stack
      className="bShadow"
      sx={{
        width: "100%",
        height: { lg: "40vh", md: "40vh", sm: "20vh", xs: "20vh" },
        backgroundImage: `url(${bgimg})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        overflow: "auto",
      }}
    >
      <Stack>
        <Typography
          className="flex title"
          variant="h1"
          sx={{
            fontSize: { lg: "40px", md: "35px", sm: "25px", xs: "20px" },
            justifyContent: "flex-end",
            marginRight: "4em",
            marginTop: "2em",
          }}
        >
          {text}
        </Typography>
      </Stack>
    </Stack>
  );
};

export default Header;
