import React, { useState } from "react";
import { useNavigate } from "react-router";
import axios from "axios";
import {
  Typography,
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  TextField,
  Grid,
  Link,
  Stack,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

const LoginModal = ({ handleClose }) => {
  const nav = useNavigate();
  const emailRegex = /.*/;
  const passRegex = /.*/;

  const [login, setLogin] = useState({ username: "test", password: "test" });

  const handleEmailValidation = (e) => {
    const reg = new RegExp(emailRegex);
    setLogin({ ...login, username: e.target.value });
  };

  const handlePassValidation = (e) => {
    const reg = new RegExp(passRegex);
    setLogin({ ...login, password: e.target.value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log(event.currentTarget);
    console.log({
      email: data.get("username"),
      password: data.get("password"),
    });

    axios({
      method: "post",
      url: "http://localhost:5000/api/login",
      data: login,
    })
      .then((res) => {
        console.log("res", res);
        if (res.data.message === "Login successful") {
          console.log("reroute good login ");
          nav("/admin/UpdateHomePage");
        }
      })
      .catch((err) => console.log("err", err));
  };
  return (
    <>
      {console.log("login", login)}
      <Box
        sx={{
          position: "fixed",
          zIndex: "10",
          left: "0",
          top: "0",
          width: "100%",
          height: "100%",
          backgroundColor: "rgba(0, 0, 0, 0.4)",
        }}
      >
        <Box
          sx={{
            zIndex: "2",
            top: "0",
            m: "15% auto",
            width: { lg: "450px", md: "400px", sm: "350px", xs: "350px" },
            height: "400px",
            backgroundColor: "#fff",
          }}
        >
          <Box
            className="disFlexCenter"
            sx={{
              marginTop: 8,
              flexDirection: "column",
            }}
          >
            <Stack
              sx={{
                display: "flex",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <Typography
                sx={{
                  textAlign: "left",
                  width: "100%",
                  ml: "20px",
                  mt: "10px",
                  fontSize: "25px",
                }}
              >
                Login
              </Typography>
              <Typography
                onClick={() => handleClose()}
                sx={{
                  width: "100%",
                  mt: "15px",
                  textAlign: "right",
                  mr: "15px",
                }}
              >
                <CloseIcon />
              </Typography>
            </Stack>
            <Box
              component="form"
              onSubmit={handleSubmit}
              sx={{
                width: "100%",
                mt: 1,
                flexDirection: "column",
                paddingLeft: "2.5em",
              }}
            >
              <TextField
                sx={{
                  width: "90%",
                }}
                margin="normal"
                required
                id="username"
                label="Username"
                name="username"
                autoComplete="username"
                autoFocus
                onChange={(e) => handleEmailValidation(e)}
                error={!login.username}
              />
              <TextField
                sx={{
                  width: "90%",
                }}
                margin="normal"
                required
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                onChange={(e) => handlePassValidation(e)}
                error={!login.password}
              />
              <FormControlLabel
                control={<Checkbox value="remember" color="primary" />}
                label="Remember me"
              />
              <Button
                className="btn"
                type="submit"
                sx={{
                  mt: 3,
                  mb: 2,
                  width: "90%",
                  backgroundColor: "#b22234",
                  color: "#fff",
                }}
              >
                Login
              </Button>
              <Grid container>
                <Grid item xs sx={{ textAlign: "center", width: "100%" }}>
                  <Link href="#" variant="body2">
                    Forgot password?
                  </Link>
                </Grid>
              </Grid>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default LoginModal;
